/*------------------------------ <RESET, COMMON PARAMETERS> ------------------------------*/
* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;

  &::before, &::after {
    padding: 0;
    margin: 0;
    border: 0;
    box-sizing: border-box;
  }
}

a {
  text-decoration: none;
}

ul, ol, li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: inherit;
  font-size: inherit;
}

html, body {
  height: 100%;
  line-height: 1;
  font-size: 16px;
  background-color: #333;
  color: #fff;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

body {

  &._lock {
    overflow: hidden;
  }
}
/*------------------------------ </RESET, COMMON PARAMETERS> -----------------------------*/

/*------------------------------ <WRAPPER> ------------------------------*/
.wrapper {
  min-height: 100%;
}
/*------------------------------ </WRAPPER> -----------------------------*/